
export default (router) => {
router.map({
	"/index": {
		name: 'index',
		component: function(resolve) {
          require(['./views/mIndex.vue'], resolve);
        },
        auth: true
	},
	"/detail": {
		name: 'detail',
		component: function(resolve) {
          require(['./views/mDetail.vue'], resolve);
        }
	},
	"/404": {
		name: '404',
		component: function(resolve) {
          require(['./views/404.vue'], resolve);
        }
	}
})
}



